.modal {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      -webkit-animation-name: animatetop;
      -webkit-animation-duration: 0.4s;
      animation-name: slide-in;
      animation-duration: 0.5s;
  }
  .modal-overlay{
    position: relative;
    transition: opacity 0.3s;
    z-index: 2000;
    display:block;
    margin: -100% auto;
    max-width:500px;
    min-height:300px;
    background-color: #fefefe;
    border-radius: 10px;
    padding: 40px 30px 50px;
    color: #333333;
}

  .modal-close-button{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 18px;
    height: 18px;
    right:30px;
    top:40px;
    font-size: large;
    transition: .5s;
    cursor: pointer;
    scale: 1.4; 
  }
  
  h3{
    margin-top:0;
    margin-bottom: 3%;
  }

  p{
    font-family: 'Montserrat Alternates';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    margin-bottom: 3%;
  }

  @media(max-width:550px){
     .modal{
      width:90%;
      margin-top: 200%;
      height:auto;
    }
  }

  /* @keyframes appear {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  } */
  
  @keyframes slide-in {
    from {
      transform: translateY(-150px);
    }
    to {
      transform: translateY(0);
    }
  }
  

  
